/* Flexbox layout for buttons */
.button-container {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: center;
  }
  
  /* Ensure all buttons are the same size */
  .button-container .btn {
    flex: 1;
    min-width: 110px;  /* Minimum width to prevent buttons from becoming too small */
    margin: 3px;
  }
  
  /* Media Query for small screens */
  @media (max-width: 600px) {
    .button-container {
      flex-direction: column;  /* Stack the buttons vertically on small screens */
      align-items: center;     /* Center buttons */
    }
    
    .button-container .btn {
      width: 100%;  /* Make buttons take up full width on smaller screens */
      margin-bottom: 5px;  /* Add space between buttons when stacked */
    }
  }
  